import React, { Component } from 'react';
import Typed from 'typed.js';
import Icon from 'react-icons-kit';
import {
  blogger,
  github,
  linkedin,
  envelop,
} from 'react-icons-kit/icomoon';
import {Helmet} from "react-helmet";

import './App.css';

class App extends Component {
  componentDidMount() {
    const strings = [
      'write { }',
      'write <i>poems</i>'
    ];
    const options = {
      strings: strings,
      typeSpeed: 50,
      backSpeed: 50,
      loop: true,
    };
    this.typed = new Typed(this.whatIdo, options);
  }

  componentWillUnmount() {
    this.typed.destroy();
  }
  render() {
    return (
      <div className="App">
        <Helmet>
          <meta charSet="utf-8" />
          <title>Sanket Thanvi</title>
          <meta name="description" content="personal website" />
        </Helmet>
        <div className="App-header">
          <h1 className="App-title">
            <b>
              Sanket Thanvi
            </b>
          </h1>
          <h1 className="App-subtitle">
            I <span
              style={{ whiteSpace: 'pre' }}
              ref={(el) => { this.whatIdo = el; }}
            />
          </h1>
        </div>
        <div className="Section-header">
          <div className="Section-icon">
            <a href="http://mypoemssanket.blogspot.com" target="_blank">
              <div style={{color:'#ffc500'}}>
                <Icon size={24} icon={blogger} />
              </div>
            </a>
          </div>
          <div className="Section-icon">
            <a href="https://github.com/sanketthanvi" target="_blank">
              <div style={{color:'#000000'}}>
                <Icon size={24} icon={github} />
              </div>
            </a>
          </div>
          <div className="Section-icon">
            <a href="https://in.linkedin.com/in/sanket-thanvi-95557288" target="_blank">
              <div style={{color:'#0e096a'}}>
                <Icon size={24} icon={linkedin} />
              </div>
            </a>
          </div>
          <div className="Section-icon">
            <a href="mailto:s.thanvi19@gmail.com" target="_blank">
              <div style={{color:'#4d0c8e'}}>
                <Icon size={24} icon={envelop} />
              </div>
            </a>
          </div>
        </div>
      </div>
    );
  }
}

export default App;



